@import url(reset.css);

div.sub8{
    h2{
        color: #00b0ff;
    }
    position: relative;
    width: 1690px;
    height: 950px;
    margin: 45px auto;
    background: #fff;
    padding-bottom: 35px;
    section{
        position: relative;
        width: 95%;
        height: 752px;
        margin: 0 auto;
        padding: 50px;
        background: #e8e8e8;
        border-radius: 35px;
        box-sizing: border-box;
        ul{
            width:80%;
            &::after{
                display: block;
                content: "";
                clear: both;
            }
            li{
                float: left;
                padding: 25px;
                background: #fff;
                border-radius: 20px;
                margin: 20px;
                &:nth-child(1){
                    padding: 10px;
                }
                .ryan{
                    position: relative;  
                    display: inline-block; 
                    width: 222px; 
                    height: 203px; 
                    z-index: 5; 
                }
                .ears{
                    position: absolute; 
                    left: 0; top: 0; bottom: 3px;  
                    z-index:1; 
                    width: 60px; height: 40px; 
                    transform: rotate(-40deg);  background: #d5972a;
                    border-radius: 60px 60px 0 0; 
                    border: 5px solid black; 
                    border-bottom: transparent;  
                    box-sizing: border-box;
                    &::after{
                        content:"";
                        position: absolute; 
                        left: 150px; bottom: 3px;  
                        background: #d5972a;
                        width: 60px; height: 40px; 
                        border-radius: 60px 60px 0 0; 
                        border: 5px solid black; 
                        border-bottom: transparent;
                        box-sizing: border-box; 
                        transform: rotate(80deg); 
                        transform-origin: -45px 50px;
                    }
                }

                .face{
                    position: absolute; 
                    width: 100%; height: 100%; 
                    border-radius: 50%;  
                    background:#d5972a;
                    border: 5px solid #000; 
                    box-sizing: border-box; 
                    z-index:10; }

                .eyeborws{
                    &::after,&::before{
                        content: ""; 
                        position: absolute; 
                        left: calc(50% - 25px); 
                        top: 65px; 
                        width: 50px; height: 8px; 
                        background: #000; 
                        border-radius: 5px;
                    }
                    &::before{margin-left: -46px;}
                    &::after{margin-left: 46px; animation:eybrow-motion 2s ease infinite; }
                }
 
                @keyframes eybrow-motion {
                    50%{transform: rotate(-20deg);}   
                }

                .eyes{
                    &::after, &::before{
                        content: ""; 
                        position: absolute; 
                        left: calc(50% - 7.5px); top: 80px; 
                        width: 15px; height: 15px; 
                        border-radius: 50%;
                        background: #000;
                    }
                    &::after{margin-left: -45px;}
                    &::before{margin-left: 45px; animation: eye-motion 2s linear infinite ;}
                }

                @keyframes eye-motion {
                    25%, 50%{height: 3px; top: 85px;}
                    
                }

                .nose{
                    position: absolute; 
                    left: calc(50% - 10px); top: 100px; 
                    width: 20px; height: 20px; 
                    border-radius: 50%; 
                    background: #000; 
                    z-index:11;}

                .mouth{
                    position: absolute; 
                    left: calc(50% - 17.5px); top: 105px; 
                    width: 35px; height: 35px; 
                    border-radius: 50%;  
                    z-index: 90; 
                    z-index:5; 
                    box-shadow:-13px 0 0 0 #fff, 13px 0 0 0 #fff ;
                    &::after,&::before{
                        content: ""; 
                        position: absolute; 
                        left: calc(50% - 20px); 
                        width: 40px; height: 40px; 
                        border-radius: 50%; 
                        border: 5px solid #000; 
                        box-sizing: border-box; 
                        background-color: #fff; 
                    }
                    &::after{
                        margin-left: -13px; 
                        transform: rotate(-10deg);    
                        border-right-color: transparent;
                    }
                    &::before{margin-left: 13px;}
                }
                .emoji{
                    position: relative; 
                    display: inline-block; 
                    width: 120px; height: 120px; 
                    border-radius: 50%;
                    margin: 15px 15px 40px; 
                    background: #ffda6a;
                    &::after{
                        content: "Emoji"; position: absolute; bottom: -40px; left: calc(50% - 30px);
                        width: 60px; font-size: 18px; color: #8a8a8a;
                    }
                }
                /* 1.emoji_like */
                .emoji_like{
                    background: #548dff;
                    &::after{
                        content: 'Like';
                    }
                }

                .emoji_like .emoji_hand{
                    position: absolute; 
                    left: 25px; bottom: 30px; 
                    width: 20px; height: 40px;
                    background: #fff; 
                    border-radius: 5px;
                    animation: hands-up 2s linear 0s infinite;
                    &::before{
                        content: "";
                        position: absolute; 
                        left: 25px; bottom: 5px;
                        width: 40px; height: 10px; 
                        background: #fff;
                        border-radius: 2px 10px 10px 2px;
                        box-shadow: 1px -9px 0 1px #fff,2px -19px 0 2px #fff,3px -29px 0 3px #fff;
                        }
                    }
                @keyframes hands-up {
                    25%{transform: rotate(15deg);}
                    50%{transform: rotate(-15deg) translateY(-10px);}
                    75%,100%{transform: rotate(0deg);}
                }
                .emoji_like .emoji_thumb{
                    position: absolute;
                    right: -25px; top: -25px; 
                    border-bottom: 20px solid #fff;
                    border-left: 20px solid transparent;
                    transform: rotate(5deg); 
                    transform-origin: 0 100%;
                    animation: thumbs-up 2s linear infinite alternate;
                    &::before{
                        content: ''; 
                        position: absolute; 
                        left: -9.7px; top: -9px; 
                        width: 10px; height: 12px;
                        background: #fff;
                        border-radius: 50% 50% 0 0; 
                        box-shadow: -1px 4px 0 -1px #fff;
                        transform: rotate(-14deg); 
                        transform-origin: 100% 100%;
                    }
                    }
                @keyframes thumbs-up {
                    25%{transform: rotate(20deg);}
                    50%,100%{transform: rotate(25deg);}
                }
                /* 2. emoji_love */
                .emoji_love{
                    background: #f55064;
                    &::after{
                        content: "Love";
                    }
                }
                .emoji_love .emoji_heart{
                    position: absolute;
                    left: 50%; top: 50%;
                    margin-left: -40px; 
                    margin-top: -40px; 
                    width: 80px; height: 80px;
                    animation:heartbeat 1s linear infinite alternate;
                }
                @keyframes heartbeat {
                    25%{transform: scale(1.1);}
                    75%{transform: scale(0.6);}
                }
                .emoji_heart{
                    &::before, &::after{
                        content: ''; 
                        position: absolute; 
                        left: 50%; top: 50%;
                        width: 40px; height: 64px; 
                        margin-left: -20px; margin-top: -32px;
                        background: #fff; 
                        border-radius: 20px 20px 0 0;
                    }
                    &::before{
                        transform-origin: 0 100%; 
                        transform: translateX(20px) rotate(-45deg);
                    }
                    &::after{
                        transform-origin: 100% 100%; 
                        transform: translateX(-20px) rotate(45deg);
                    }
                }
                /* 3.emoji_haha */
                .emoji_haha{
                    &::after{
                        content: "Haha";
                        }
                    }
                .emoji_haha .emoji_face{
                    position: absolute; 
                    width: 100%; height: inherit;
                    animation:haha-face 2s linear infinite normal ;
                    }
                @keyframes haha-face{
                    10%{transform: translateY(25px);}
                    20%{transform: translateY(15px);}

                    30%{transform: translateY(25px);}
                    40%{transform: translateY(15px);}

                    50%{transform: translateY(25px);}

                    60%{transform: translateY(0);}
                    70%{transform: translateY(-10px);}

                    80%{transform: translateY(0);}
                    90%{transform: translateY(-10px);}
                    
                }
                .emoji_haha .emoji_eyes{
                    position: absolute; 
                    left: 50%; top: 35px; 
                    margin-left: -13px; 
                    width: 26px; height: 6px; 
                    border-radius: 2px; 
                    box-shadow: -25px 5px 0 0 #000,25px -5px 0 0 #000;
                    transform: rotate(20deg);
                    &::after{
                        content: "";
                        position: absolute;
                        left: 0; top: 0; 
                        width: 26px; height: 6px;
                        border-radius: 2px; 
                        box-shadow: -25px -5px 0 0 #000,25px 5px 0 0 #000;
                        transform: rotate(-40deg);
                        }
                    }
                                
                .emoji_haha .emoji_mouth{
                    overflow: hidden; 
                    position: absolute; 
                    left: 50%; top: 50%; 
                    width: 80px; height: 40px;
                    margin-left: -40px; 
                    background: #000; 
                    border-radius: 0 0 40px 40px;
                    animation:haha-mouth 2s linear infinite normal ;
                    }
                @keyframes haha-mouth {
                    10%{transform: scale(0.6); top: 45%;}
                    20%{transform: scale(0.8); top: 45%;}

                    30%{transform: scale(0.6); top: 45%;}
                    40%{transform: scale(0.8); top: 45%;}

                    50%{transform: scale(0.6); top: 45%;}

                    60%{transform: scale(1); top: 50%;}
                    70%{transform: scale(1.2); top: 50%;}

                    80%{transform: scale(1); top: 50%;}
                    90%{transform: scale(1.1); top: 50%;}
                }
                .emoji_haha .emoji_tongue{
                    position: absolute;
                    left: 50%;bottom: -10px;
                    width: 70px;height: 30px;
                    margin-left:-35px; 
                    background: #f55064;
                    border-radius: 50%;
                    }
                /* emoji_yay */
                .emoji_yay{
                    &::after{
                        content:"Yay";
                    }      
                }
                .emoji_yay .emoji_face{
                    position: absolute; 
                    width: 100%; height: inherit;
                    animation:yay 1s linear infinite alternate;
                    }
                @keyframes yay {
                    25%{transform: rotate(-15deg);}
                    75%{transform: rotate(15deg);}
                }
                .emoji_yay .emoji_eyeborws{
                    position: absolute; 
                    left: calc(50% - 3px); 
                    top: 30px; 
                    width: 6px; height: 6px; 
                    border-radius: 50%;
                    box-shadow: -6px 0 0 0 #000,-36px 0 0 0 #000, 
                                6px 0 0 0 #000, 36px 0 0 0 #000;
                    &::before,&::after{
                        content:"";
                        position: absolute; 
                        left: calc(50% - 18px); bottom: 3px; 
                        width: 36px; height: 18px;
                        border-radius: 60px 60px 0 0; 
                        border: 6px solid black; 
                        border-bottom: 0; box-sizing: border-box;
                        }
                    &::before{
                        margin-left: -21px;
                        }
                    &::after{
                        margin-left: 21px;
                        }
                    }
                .emoji_yay .emoji_mouth{
                    position: absolute; 
                    left: calc(50% - 3px); top: 60px;
                    width: 6px; height: 6px; 
                    border-radius: 50%;
                    box-shadow: -25px 0 0 0 #000, 25px 0 0 0 #000,
                                -35px -2px 30px 10px #D5234C,35px -2px 30px 10px #D5234C;
                    &::after{
                        content: ""; 
                        position: absolute; 
                        left: calc(50% - 40px);
                        top: -64px; 
                        width: 80px; height: 80px; 
                        border-radius: 50%; 
                        border: 6px solid #000;
                        box-sizing: border-box; 
                        border-top-color: transparent; 
                        border-right-color: transparent;
                        border-left-color: transparent;
                        }
                    }
                /* emoji_wow */
                .emoji_wow{
                    &::after{
                        content: "wow";
                    }
                }
                .emoji_wow .emoji_face{
                    position: absolute; 
                    width: 100%; height: inherit;
                    animation:wow-face 3s linear 0s infinite ;
                }
                @keyframes wow-face {
                    15%,25%{transform: rotate(20deg) translateX(-25px);}
                    45%,65%{transform: rotate(-20deg) translateX(25px);}
                    75%,100%{transform: rotate(0deg) translateX(0);}
                    
                }
                .emoji_wow .emoji_eyeborws{
                    position: absolute; 
                    left: calc(50% - 3px); 
                    width: 6px; height: 6px;
                    border-radius: 50%; 
                    box-shadow: -18px 0 0 0 #000,-33px 0 0 0 #000,18px 0 0 0 #000,
                                33px 0 0 0 #000;
                    animation:wow-brow 3s linear 0s infinite ;
                    &::after,&::before{
                        content: ""; 
                        position: absolute;
                        left: calc(50% - 12px); top: -3px; 
                        width: 24px; height: 20px; 
                        border-radius: 50%;
                        border: 6px solid #000; 
                        box-sizing: border-box; 
                        border-right-color: transparent;
                        border-bottom-color: transparent; 
                        border-left-color: transparent;
                        }
                    &::before{margin-left: -25px;}
                    &::after{margin-left: 25px;}
                    }
                @keyframes wow-brow {
                    15%,65%{top:25px;}
                    75%,100%,0%{top: 15px;}
                    
                }
                .emoji_wow .emoji_eyes{
                    position: absolute; 
                    left: calc(50% - 8px); top: 35px; 
                    width: 16px; height: 24px;
                    border-radius: 50%; 
                    box-shadow: 25px 0 0 0 #000,-25px 0 0 0 #000;
                }
                .emoji_wow .emoji_tongue{
                    position: absolute; 
                    left:calc(50% - 15px); top: 50%;
                    width: 30px; height: 45px;
                    border-radius: 50%; 
                    background: #000;
                    animation:wow-mouth 3s linear 0s infinite ;
                }
                @keyframes wow-mouth {
                    10%,30%{width: 20px; height: 20px;left: calc(50% - 10px);}
                    50%,70%{width: 30px; height: 40px;left: calc(50% - 15px);}
                    75%,100%{height: 50px;}
                    
                }
                /* emoji_sad */
                .emoji_sad{&::after{content: "sad";}}
                .emoji_sad .emoji_face{
                    position: absolute; 
                    width: 100%; height: inherit;
                    animation:sad-face 2s ease-in 0s infinite ;
                }
                @keyframes sad-face {
                    25%, 35%{top:-15px;}
                    55%, 95%{top:10px;}
                    100%, 0%{top:0;}
                    
                }
                .emoji_sad .emoji_eyeborws{
                    position: absolute; 
                    left: calc(50% - 3px); top: 35px; 
                    width: 6px; height: 6px;
                    border-radius: 50%; 
                    box-shadow: -40px 9px 0 0 #000,-25px 0 0 0 #000,
                                25px 0 0 0 #000,40px 9px 0 0 #000;
                    &::after,&::before{
                        content: ""; 
                        position: absolute;
                        left: calc(50% - 15px); top: 2px; 
                        width: 30px; height: 20px; 
                        border: 6px solid #000;
                        box-sizing: border-box; 
                        border-radius: 50%; 
                        border-left-color: transparent; 
                        border-right-color: transparent; 
                        border-bottom-color: transparent;
                        }
                    &::before{
                        margin-left: -30px; 
                        transform: rotate(-30deg);}
                    &::after{
                        margin-left: 30px; 
                        transform: rotate(30deg);
                    }
                    }
                .emoji_sad .emoji_eyes{
                    position: absolute; 
                    left: calc(50% - 7px); top: 50px; 
                    width: 14px; height: 16px; 
                    border-radius: 50%;  
                    box-shadow: 25px 0 0 0 #000,
                                -25px 0 0 0 #000;
                    &::after{
                        content: ""; 
                        position: absolute; 
                        width: 12px; height: 12px;
                        margin-left: 6px; 
                        background: #548dff;
                        border-radius: 0 100% 40% 50% / 0 50% 40% 100%;
                        transform-origin: 0% 0%; animation:tear-drop 2s ease-in infinite ;
                    }
                }

                @keyframes tear-drop {
                    0%,100%{display: block; left: 35px; top: 15px; transform: rotate(45deg) scale(0);}
                    25%{display: block; left: 35px; top: 15px; transform: rotate(45deg) scale(2);}
                    49.9%{display: block; left: 35px; top: 65px; transform: rotate(45deg) scale(0);}
                    50%{display: block; left: -35px; top: 15px; transform: rotate(45deg) scale(0);}
                    75%{display: block; left: -35px; top: 15px; transform: rotate(45deg) scale(2);}
                    99.9%{display: block; left: -35px; top: 65px; transform: rotate(45deg) scale(0);}
                    
                }
                .emoji_sad .emoji_mouth{
                    position: absolute; 
                    left: calc(50% - 3px); top: 90px; 
                    width: 6px; height: 6px;
                    border-radius: 50%; 
                    box-shadow: -18px 0 0 0 #000,18px 0 0 0 #000;
                    &::after{
                        content: ""; 
                        position: absolute; 
                        left: calc(50% - 30px); top: -10px;
                        width: 60px; height: 80px; 
                        box-sizing: border-box; 
                        border: 6px solid #000; 
                        border-radius: 50%;
                        border-right-color: transparent; 
                        border-bottom-color: transparent; 
                        border-left-color: transparent;
                    }
                } 
                /* emoji_angry */
                .emoji_angry{
                    background: linear-gradient(to bottom, #D5234C -10%,#FFDA6A 100%); 
                    background-size: 100%;
                }
                .emoji_angry{&::after{content: "angry";}}
                .emoji_angry .emoji_face{
                    position: absolute; 
                    width: 100%; height: inherit;
                    animation:angry-face 2s ease-in 0s infinite ;
                }
                @keyframes angry-face {
                    35%,60%{transform: translateX(0) translateY(10px) scale(0.9);}
                    40%{transform: translateX(-5px) translateY(10px) scale(0.9);}
                    45%{transform: translateX(5px) translateY(10px) scale(0.9);}
                    50%{transform: translateX(-5px) translateY(10px) scale(0.9);}
                    55%{transform: translateX(5px) translateY(10px) scale(0.9);}
                }

                .emoji_angry .emoji_eyeborws{
                    position: absolute; 
                    left: calc(50% - 3px); top: 55px; 
                    width: 6px; height: 6px;
                    border-radius: 50%; 
                    box-shadow: -44px 5px 0 0 #000,-7px 16px 0 0 #000,
                    7px 16px 0 0 #000,44px 5px 0 0 #000;
                    &::before, &::after{
                        content: ""; 
                        position: absolute;
                        left: calc(50% - 25px); top: 0; 
                        width:50px; height: 20px; 
                        border: 6px solid #000;
                        border-radius: 50%; box-sizing: border-box; 
                        border-top-color: transparent; 
                        border-right-color: transparent; 
                        border-left-color: transparent;
                    }
                    &::before{
                        margin-left: -25px; 
                        transform: rotate(15deg);
                    }
                    &::after{
                        margin-left: 25px; 
                        transform: rotate(-15deg);
                    }
                }
                .emoji_angry .emoji_eyes{
                    position: absolute; 
                    left:calc(50% - 6px); top: 70px;
                    width: 12px;height: 12px;
                    border-radius: 50%;
                    box-shadow: 25px 0 0 0 #000,-25px 0 0 0 #000;
                }
                .emoji_angry .emoji_mouth{
                    position: absolute; 
                    left: calc(50% - 18px);bottom: 15px;
                    width: 36px;height: 18px;
                    background: #000;
                    border-radius: 50%;
                    animation:angry-mouth 2s ease-in 0s infinite ;
                }
                @keyframes angry-mouth {
                    25%, 50%{height: 6px; bottom: 25px;}
                    
                }
            }
        }
    }
}